:root { 
    --fontSize: 20px;
    --fontColor: white;
    --fontStyle:  'Poppins', sans-serif;
    --buttonColor: #EA445A;
}

/* Navigation bar */
.navBar {
    font-family: var(--fontStyle);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: rgba(43, 43, 43, 0.3);
  }
  
.navBar ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    list-style-type: none;
  }
  
  .navBar ul li {
    display: inline-block;
  }
  
  .navBar ul li img {
    width: 5vw;
    opacity: 0.5;
    animation: opacity 2s linear infinite;
    overflow: hidden;
  }
  
  @keyframes opacity {
    0% {opacity: 0.5;}
    50% {opacity: 1;}
    100% {opacity: 0.5;}
  }
  
  .navBar ul li a {
    display: inline-block;
    font-size: var(--fontSize);
    color: var(--fontColor);
    text-decoration: none;
    margin: 0 5vw;
    transition: color 0.5s;
    transform-origin: center;
  }
  
  
  .navBar ul li a:hover {
    color: var(--buttonColor);
  }