:root {
    --fontColor: white;
    --fontStyle:  'Poppins', sans-serif;
}

/*Home page*/
.homePage {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.homePage img {
    margin-top: 90px;
    width: 25%;
    border-radius: 50%;
}

.socialWrap {
    position: fixed;
    left: 0;
}

.socialMedia ul {
    list-style-type: none;
}

.socialNameWrap a img {
    width: 3.5vw;
    transition: 0.5s;
}

.socialNameWrap a img:hover {
    transform: rotate(-90deg);
}

.socialNameWrap {
    display: flex;
    align-items: center;
}

/*10*/
.socialText {
    color: var(--fontColor);
    transform: translate(-40px, 42px);
    text-decoration: none;
    z-index: -1;
}